'use client'

import Title from '@/app/components/Admin/Title'
import React, { useState } from 'react'
import Image from 'next/image'
import { assets } from '@/app/assets/assets'
import { Input } from '@/app/components/ui/input'
import { Textarea } from '@/app/components/ui/textarea'
import { Form, FormField, FormItem, FormLabel, FormControl } from '@/app/components/ui/form'
import { useForm } from 'react-hook-form'

const AddTablePage = () => {
    const [image, setImage] = useState<string | File>('')
    const form = useForm({
        defaultValues: {
            number: '',
            capacity: 0,
            section: '',
            image: '',
            type: '',
            isWindow: false,
            pricePerSlot: 0,
            location: '',
            description: '',
            isAvailable: true,
            createdAt: new Date().toISOString(),
        }
    })

    interface TableFormData {
        number: string
        capacity: number
        section: string
        image: string
        type: string
        isWindow: boolean
        pricePerSlot: number
        location: string
        description: string
        isAvailable: boolean
        createdAt: string
    }

    const onSubmit = async (data: TableFormData) => {
        console.log(data)
    }

    return (
        <div className='px-6 py-8 flex-1 bg-gray-50'>
            <Title title='Add Table' subTitle='Add a new table to the system' />

            <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="bg-white p-6 rounded-lg border border-gray-200 flex flex-col gap-6 text-gray-600 text-sm mt-6 max-w-xl">
                    {/* Table Image */}
                    <div className='flex items-center gap-4'>
                        <label htmlFor="table-image" className="cursor-pointer">
                            <div className="relative w-24 h-24 rounded-lg bg-gray-100 border-2 border-dashed border-gray-300 flex items-center justify-center hover:bg-gray-50 transition-colors">
                                {image instanceof File ? (
                                    <Image
                                        src={URL.createObjectURL(image)}
                                        alt="table preview"
                                        fill
                                        className='object-fill rounded-lg'
                                    />
                                ) : (
                                    <Image
                                        src={assets.upload_icon}
                                        alt="upload"
                                        className='opacity-50 w-full h-full'
                                    />
                                )}
                            </div>
                            <input
                                type='file'
                                id='table-image'
                                accept='image/*'
                                hidden
                                onChange={e => e.target.files?.[0] && setImage(e.target.files[0])}
                            />
                        </label>
                        <div>
                            <p className='font-medium text-gray-800'>Table Image</p>
                            <p className='text-sm text-gray-500 mt-1'>Upload a picture of the table</p>
                        </div>
                    </div>

                    {/* Table Details */}
                    <div className='grid md:grid-cols-2 gap-4'>
                        <FormField
                            control={form.control}
                            name="number"
                            render={({ field }) => (
                                <FormItem>
                                    <Input
                                        label="Table Number"
                                        placeholder="e.g. T01"
                                        {...field}
                                    />
                                </FormItem>
                            )}
                        />

                        <FormField
                            control={form.control}
                            name="capacity"
                            render={({ field }) => (
                                <FormItem>
                                    <Input
                                        type="number"
                                        label="Capacity"
                                        placeholder="Number of guests"
                                        {...field}
                                    />
                                </FormItem>
                            )}
                        />

                        <FormField
                            control={form.control}
                            name="section"
                            render={({ field }) => (
                                <FormItem>
                                    <FormLabel>Section</FormLabel>
                                    <Select onValueChange={field.onChange} defaultValue={field.value}>
                                        <SelectTrigger>
                                            <SelectValue placeholder="Select section" />
                                        </SelectTrigger>
                                        <SelectContent>
                                            <SelectItem value="Main Hall">Main Hall</SelectItem>
                                            <SelectItem value="Terrace">Terrace</SelectItem>
                                            <SelectItem value="Private Room">Private Room</SelectItem>
                                        </SelectContent>
                                    </Select>
                                </FormItem>
                            )}
                        />

                        <FormField
                            control={form.control}
                            name="type"
                            render={({ field }) => (
                                <FormItem>
                                    <FormLabel>Type</FormLabel>
                                    <Select onValueChange={field.onChange} defaultValue={field.value}>
                                        <SelectTrigger>
                                            <SelectValue placeholder="Select type" />
                                        </SelectTrigger>
                                        <SelectContent>
                                            <SelectItem value="Standard">Standard</SelectItem>
                                            <SelectItem value="Family">Family</SelectItem>
                                            <SelectItem value="Premium">Premium</SelectItem>
                                            <SelectItem value="VIP">VIP</SelectItem>
                                        </SelectContent>
                                    </Select>
                                </FormItem>
                            )}
                        />

                        <FormField
                            control={form.control}
                            name="pricePerSlot"
                            render={({ field }) => (
                                <FormItem>
                                    <Input
                                        type="number"
                                        label="Price per Slot"
                                        placeholder="Price in USD"
                                        {...field}
                                    />
                                </FormItem>
                            )}
                        />

                        <FormField
                            control={form.control}
                            name="location"
                            render={({ field }) => (
                                <FormItem>
                                    <FormLabel>Location</FormLabel>
                                    <Select onValueChange={field.onChange} defaultValue={field.value}>
                                        <SelectTrigger>
                                            <SelectValue placeholder="Select location" />
                                        </SelectTrigger>
                                        <SelectContent>
                                            <SelectItem value="Ground Floor">Ground Floor</SelectItem>
                                            <SelectItem value="First Floor">First Floor</SelectItem>
                                            <SelectItem value="Rooftop">Rooftop</SelectItem>
                                        </SelectContent>
                                    </Select>
                                </FormItem>
                            )}
                        />
                    </div>

                    {/* Additional Options */}
                    <div className='space-y-3'>
                        <FormField
                            control={form.control}
                            name="isWindow"
                            render={({ field }) => (
                                <FormItem className="flex items-center gap-2">
                                    <FormControl>
                                        <Checkbox
                                            checked={field.value}
                                            onCheckedChange={field.onChange}
                                        />
                                    </FormControl>
                                    <FormLabel className="!mb-0">Window View</FormLabel>
                                </FormItem>
                            )}
                        />

                        <FormField
                            control={form.control}
                            name="isAvailable"
                            render={({ field }) => (
                                <FormItem className="flex items-center gap-2">
                                    <FormControl>
                                        <Checkbox
                                            checked={field.value}
                                            onCheckedChange={field.onChange}
                                        />
                                    </FormControl>
                                    <FormLabel className="!mb-0">Available for Booking</FormLabel>
                                </FormItem>
                            )}
                        />
                    </div>

                    {/* Description */}
                    <FormField
                        control={form.control}
                        name="description"
                        render={({ field }) => (
                            <FormItem>
                                <Textarea
                                    label="Description"
                                    placeholder="Describe the table and its features..."
                                    rows={3}
                                    {...field}
                                />
                            </FormItem>
                        )}
                    />

                    {/* Submit Button */}
                    <button
                        type="submit"
                        className='bg-primary text-white font-medium px-6 py-2 rounded-lg hover:bg-primary/90 transition-colors mt-4'
                    >
                        Add Table
                    </button>
                </form>
            </Form>
        </div>
    )
}

export default AddTablePage
